.works {
    padding: 0 3rem 0 3rem;
    display: flex;
    height: 90vh;
    margin-top: 4rem;

}

/* right side */
.w-right {
    position: relative;
}

.w-mainCircle {
    left: 9rem;
    position: relative;
    width: 18rem;
    height: 18rem;
    border-radius: 100%;
    box-shadow: var(--smboxShadow);
    top: 2rem;
    background: white;
}

.w-mainCircle>* {
    position: absolute;
}

.w-mainCircle> :nth-child(1) {
    top: -3rem;
    left: 6rem;
}

.w-mainCircle> :nth-child(2) {
    top: 5rem;
    left: -3rem;
}

.w-mainCircle> :nth-child(3) {
    top: 5rem;
    left: 6rem;
}

.w-mainCircle> :nth-child(4) {
    top: 5rem;
    left: 15rem;
}

.w-mainCircle> :nth-child(5) {
    top: 13rem;
    left: 6rem;
}

.w-secCircle {
    width: 6rem;
    height: 6rem;
    position: absolute;
    border-radius: 100%;
    border: 5px solid #ebebeb;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: var(--smboxShadow);
    background: white;
}

.w-secCircle>img {
    transform: scale(.75);
}

.w-backCircle {
    position: absolute;
    height: 13rem;
    width: 13rem;
    border-radius: 100%;
    z-index: -1;
}

.blueCircle {
    background: #194988;
    left: 18rem;
    top: 0rem;
}

.yellowCircle {
    background: #F5C32C;
    left: 18rem;
    top: 8rem;
}